#header{
    height: 100px;
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    /* grid-template-columns: 260px 900px 500px; */
}
.logo{
    margin-top: 15px;
    width: 180px;
    height: 50px;
    /* float: left; */
    margin-left: 70px;
    box-sizing: border-box;
}
.List-menu{
    /* display: inline; */
    width: 80rem;
    padding-top: 2rem;
    box-sizing: border-box;
    /* float: left; */
}

.Menu-item{
    min-width: 80px;
    height: fit-content;
    text-align: center;
    font-size: 20px;
    padding: 14px 19px 11px 20px;
    cursor: pointer;
    display: inline;
}
.Menu-item a{
    color: black;
    text-decoration: none;
}
.Menu-item:hover{
    opacity: 1;
    background-color: rgba(194, 53, 100, 0.05);
    border-color: rgba(204, 70, 115, 0.1);
}

#button{
    margin-top: 30px;
}

.btn_{
    background-color: white;
    font-size: 20px;
    height: 30px;
    cursor: pointer;
    /* border-radius: 6px; */
    border: none;
    display: flexbox;
    justify-content: space-between;
}
.btn_Login:hover{
    color: white;
    background-color: aqua;
}
.btn_Register{
    padding-left: 5px;
    background-color: white;
    font-size: 20px;
    height: 30px;
    cursor: pointer;
    border-radius: 6px;
    border: none;
}
.btn_Register:hover{
    color: white;
    background-color: red;
}